<template>
  <div class="bg-white">
    <section class="bg-white text-white py-6">
         <!-- <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->

         <article class="bg-blue pb-12">
            <h2 class="text-3xl py-6 text-white
            md:text-6xl
            lg:text-4xl">- Contactenos -</h2>
            <!-- <span class="font-Clarissa text-5xl">Contactenos</span> -->            

            <section class="container border-dashed border-2 rounded border-white p-5">
              <form class="flex gap-x-3 gap-y-6 flex-wrap shadow-round bg-darkBlue text-xl px-10 py-8"
              @submit="formSubmit">
                <input name="name" 
                type="text" 
                placeholder="Full Name" 
                v-model="name" 
                min="0" 
                class="w-1/2 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">

                <input name="phone" 
                type="text" 
                placeholder="Phone" 
                v-model="phone" 
                min="0" 
                class="w-1/5 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">

                <input name="email" 
                type="email" 
                placeholder="Email" 
                v-model="email" 
                min="0" 
                class="w-1/4 border rounded-4xl text-orange bg-darkBlue text-left px-6 py-2">

                <textarea name="comentarios" id="" cols="100" rows="5" 
                placeholder="Comments"
                class="border rounded-4xl text-orange bg-darkBlue text-left px-8 py-6"
                ></textarea>

                <div class="w-full flex gap-3" @click="activeTerm = !activeTerm">  
                  <span class="cursor-pointer border rounded-4xl text-orange bg-darkBlue px-2 py-1"
                  :class="(activeTerm) ? 'text-orange' : 'text-darkBlue'">
                    <i class="fas fa-check"></i>
                  </span>
                  <label class="text-orange self-center" for="term">Term and condition</label>
                </div>

                <div class="flex flex-col gap-3 w-full
                md:flex-row">
                  <span class="bg-red rounded-xl p-2 px-4 text-left
                  md:w-3/5" v-show="error">{{ error_message }}</span>
                  <span class="bg-green-500 rounded-xl p-2 px-4 text-left
                  md:w-3/5" v-show="success">La solicitud fue enviada exitosamente!!!</span>
                  <button type="button" class="uppercase bg-orange text-white text-xl rounded-4xl px-12 py-1 md:ml-auto"
                  @click="formSubmit">Enviar Solicitud</button>
                </div>                
              </form>
            </section>
          </article>
         
    </section>
    
  </div>
</template>
<script>
import Authorization from "@/services/user/auth.service";

export default {
  name: "ContactComp",
  data() {
    return {
      success: false,   
      error_message: 'Error. No se pudo enviar la solicitud!!!',   
      error: false,               
      activeTerm: false,
      name: '',
      phone: '',
      email: '',      
    }
  },
  methods: {
    async formSubmit() {
      const body = {
        subject: this.subject,
        mail: this.email,
        message: {
          name: this.name,
          phone: this.phone,
          email: this.email,
          message: this.message,
          msg_info: this.msg_info
        },
      }  

      if(this.validEmail(this.email)) {        
        if(await Authorization.checkLogin('/contact-us', {body, auth: 'bearer'}, 'post')) {
          localStorage.removeItem('request_info')
          this.success = true 
          setTimeout(() => {
              this.success = false
          }, 3000); 
        } else {
          this.error = true 
          setTimeout(() => {
              this.error = false
          }, 3000);          
        }
      } else {
        this.error = true 
        this.error_message = 'Debe insertar una dirección de correo válida.'
          setTimeout(() => {
              this.error_message = 'Error. No se pudo enviar la solicitud!!!'
              this.error = false
          }, 3000);          
      }
    },    
    validEmail(mail) {
      if(mail) {
        if(mail.match(/^[\w-.]+@([\w-]+\.)+[\w-]{2,8}$/))
          return true
      }
      return false
    },
  }
};
</script>
